import SignupPage from "../components/SignupPage";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import { registerUser, validateToken } from "../store/actions/LoginActions";
import { useSnackbar } from "notistack";
const SignUp = (props) => {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const handleSignUp = async (params) => {
    if (params != null) {
      try {
        const res = await props.registerUser(params);
        enqueueSnackbar("Signup Successful", { variant: "success" });
        navigate("/login");
      } catch (err) {
        console.log(err);
      }
    }
  };

  return (
    <div>
      <SignupPage navigate={navigate} handleSignUp={handleSignUp} />
    </div>
  );
};

function mapStateToProps(state) {
  return {
    userData: state.LoginReducer,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    registerUser: (params) => dispatch(registerUser(params)),
    validateToken: (params) => dispatch(validateToken(params)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(SignUp);
